
function getGreeting() {
    const date = new Date();
    const hour = date.getHours();
    
    let greeting;
    if (hour >=4 && hour <=10) {                // setting the greeting according to the day part
        greeting = ' ';
    } else if (hour >= 11 && hour <=16) {
        greeting = ' ';
    } else if (hour >= 17 && hour <=20) {
        greeting = ' ';
    } else {
        greeting = ' ';
    }
    
    return greeting;
}

export default getGreeting;